.App .Shop-page {
  height: auto;
  overflow: hidden;
}
.Shop-page > header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 545x;
}

.Shop-page h2 {
  width: 100%;
  text-align: center;
  font-size: 2.4rem;
  margin: 1em 0;
}

.Shop-page .filter {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  z-index: 100;
}

.Shop-page .filter > ul {
  position: fixed;
  top: 40%;
  right: 1em;
}

.Shop-page .filter > ul li {
  display: flex;
  justify-content: flex-end;
  margin-top: 1em;
}

.Shop-page .filter > ul li span {
  display: inline-block;
  margin: auto 0;
}

.Shop-page .filter > ul li span:first-of-type {
  color: #fff;
  text-align: right;
  padding: 0.3em 0.8em;
  background-color: rgba(0, 0, 0, 0.5);
}

.Shop-page .filter > ul li span:last-of-type {
  display: flex;
  width: 36px;
  height: 36px;
  background-color: #000;
  border-radius: 50%;
  margin-left: 0.6em;
}

.Shop-page .filter > ul li span:last-of-type img {
  width: 14px;
  height: 14px;
  margin: auto;
}

.Shop-page .filter > ul li .Shop-page > footer {
  width: 100%;
  text-align: center;
  margin-bottom: 3em;
}

.Shop-page .filter > div {
  display: flex;
  justify-content: flex-end;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.Shop-page .filter > div > div {
  position: absolute;
  width: 100%;
  height: 100vh;
}

.Shop-page form {
  position: relative;
  background-color: rgba(0, 0, 0, 1);
  width: 300px;
  float: right;
  height: 100%;
  padding: 1em 1.5em;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  z-index: 10;
}

.Shop-page form h3 {
  font-size: 1.41575rem;
  font-weight: 100;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  padding-bottom: 1em;
  margin-bottom: 0.8em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.Shop-page form > ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
}

.Shop-page form > ul > li {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  background-color: black;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0.8em 0 1em;
  background-color: black;
  overflow: hidden;
}

.Shop-page form > ul li a {
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
  font-size: 0.875rem;
  margin-left: 1em;
  margin-bottom: 0.5em;
}

.Shop-page form > ul > li > ul {
  width: 100%;
  margin-top: 0.8em;
  margin-left: 0.8em;
  position: relative;
  z-index: 1;
}

.Shop-page form > ul > li > ul > li {
  display: flex;
  justify-content: space-between;
  margin-top: 0.5em;
}

.Shop-page form ul img {
  width: 14px;
  height: 14px;
}

.Shop-page > footer > button {
  font-size: 1rem;
  width: 180px;
  text-align: center;
  margin: 0 auto;
  padding: 1em;
  background-color: transparent;
}

.Shop-page .product-filter ul li {
  width: 100%;
  text-align: left;
  display: flex;
  margin-top: 1em;
  justify-content: space-between;
}

.Shop-page .product-filter ul li span {
  font-size: 0.875rem;
  display: inline-block;
  color: #fff;
  margin: auto 0;
}

.Shop-page .product-filter ul li span:nth-child(1) {
  display: flex;
  width: 16px;
  height: 16px;
  background-color: rgba(90, 90, 90, 1);
  margin-right: 0.5em;
  border-radius: 1px;
}

.Shop-page .product-filter ul li span:nth-child(1) input {
  position: absolute;
  margin: auto;
  opacity: 0;
}

.Shop-page .product-filter ul li span:nth-child(1) img {
  margin: auto;
  width: auto;
  height: 10px;
}

.Shop-page .product-filter ul li span:nth-child(2) {
  width: 85%;
}

@media (min-width: 30em) {
  .Shop-page section {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    /* margin: 3em auto; */
  }

  .Shop-page form {
    width: 350px;
  }
}
