.App-header {
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0 0px 3px rgba(0, 0, 0, 0.1);
  z-index: 3;
  background-color: #fff;
}

.App-header p {
  font-size: 0.875rem;
  font-weight: 100;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  padding: 0.5em 0;
  background-color: red;
}

.App-header nav {
  display: flex;
  justify-content: space-between;
  width: 86%;
  margin: 0 auto;
  padding: 1em 0;
}

.App-header nav h1 {
  display: flex;
}

.App-header nav h1 .menu {
  margin-right: 0.5em;
}

.App-header nav h1 span img {
  height: 28px;
}

.App-header nav h1 span {
  display: inline-block;
  margin: auto 0;
}

.App-header nav .close-menu {
  display: none;
}

.App-header nav .open-menu {
  display: flex;
  flex-direction: column;
  width: 256px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  padding: 1em 0;
  background-color: #000;
  z-index: 10;
}

.App-header nav .open-menu li {
  width: 100%;
}

.App-header nav .open-menu li a {
  color: rgba(255, 255, 255, 0.8);
  display: block;
  padding: 1em 2em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.App-header nav .account {
  display: flex;
  justify-content: space-between;
  width: 180px;
  margin: auto 0;
}

.App-header nav .account > img {
  width: auto;
  height: 14px;
}

.App-header nav .account > li {
  margin: auto 0;
}

.App-header nav .account li a span {
  text-align: center;
  color: #fff;
  display: inline-block;
  font-size: 0.75rem;
  position: absolute;
  width: 20px;
  height: 20px;
  margin-top: -1em;
  margin-left: -1em;
  padding: 0.25em 0.5em;
  background-color: red;
  border-radius: 50%;
}

.App-header nav .account > li:last-of-type {
  padding: 0.5em 1em;
  background-color: red;
}

.App-header nav .account > li:last-of-type a {
  color: #fff;
}

.App-header .account > li > span {
  position: relative;
  display: block;
  position: absolute;
  width: 100%;
  top: 93px;
  left: 0;
  background-color: #fff;
}

.App-header .account > li > span > span {
  display: block;
  height: 250px;
  overflow: hidden;
  overflow-y: scroll;
}

.App-header .account > li > span > ul {
  padding: 1em;
  margin: 1em 0;
}

.App-header .account > li > span > ul li {
  display: flex;
  justify-content: space-between;
}

.App-header .account > li > span > ul > li:last-of-type {
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1em;
}

.App-header .account > li > span > ul > li:last-of-type a {
  text-align: center;
  color: #fff;
  width: 220px;
  margin: 0.8em 5% 0;
  padding: 1em;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 35px;
}

@media (min-width: 30em) {
  .App-header nav h1 span img {
    height: 36px;
  }

  .App-header nav .account {
    width: 240px;
  }

  .App-header nav .account > li > img {
    height: 18px;
  }

  .App-header nav .account > li:last-of-type {
    padding: 1em 2.4em;
  }

  .App-header .account > li > span {
    width: 350px;
    top: 108px;
    right: 0;
    left: auto;
  }

  .App-header nav .open-menu {
    width: 340px;
  }

  .App-header nav .open-menu li a {
    font-size: 1.25rem;
  }
}

@media (min-width: 50em) {
  .App-header nav h1 .menu {
    display: none;
  }

  .App-header nav ul.close-menu {
    font-weight: 600;
    width: 35%;
    display: flex !important;
    justify-content: space-between;
    margin: auto 0;
  }

  .animate__slideInRight {
    animation-name: none !important;
  }
}
