.App .Product-cart {
  width: 100%;
  height: auto;
  padding: 2em 0;
  background-color: rgba(0, 0, 0, 0.03);
}

.Product-cart footer {
  width: 84%;
  margin: 0 auto;
}

.Product-cart footer input {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  width: 100%;
  padding: 1em;
  margin-bottom: 1em;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.Product-cart footer h3 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

.Product-cart footer p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  margin-bottom: 1em;
  line-height: 1.25rem;
}

.Product-cart footer button {
  position: relative;
  color: #fff;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50px;
  background-color: rgba(0, 0, 0, 1);
}

.Product-cart footer button span {
  font-size: 1rem;
  display: block;
  margin: auto 0;
}

.Product-cart footer button img {
  position: absolute;
  top: 1em;
  right: 1em;
  height: 18px;
  /* margin: auto 0; */
}

.Product-cart footer div {
  text-align: center;
  margin: 2em 0;
}

.Product-cart footer div h4 {
  display: flex;
  justify-content: space-between;
}

.Product-cart footer div h4 span {
  display: block;
  width: 20%;
  height: 1px;
  margin: auto 0;
  background-color: rgba(0, 0, 0, 0.2);
}

.Product-cart footer div > img {
  margin-top: 1em;
}

@media (min-width: 30em) {
  .Product-cart footer {
    width: 50%;
    margin-left: 42%;
  }
}

@media (min-width: 50em) {
  .App .Product-cart {
    padding: 3em 7%;
    display: flex;
    justify-content: space-between;
  }

  .Product-cart section {
    width: 65%;
  }

  .Product-cart footer {
    width: 30%;
    margin: 0;
  }
}
