.App .Wishlist-page {
  width: 100%;
  height: auto;
  padding: 2em 0;
  background-color: rgba(0, 0, 0, 0.03);
}

.Wishlist-page section {
  width: 100%;
}

.Wishlist-page footer {
  margin: 0 auto;
  margin-top: 1em;
  background-color: #fff;
}

.Wishlist-page footer input {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  width: 100%;
  padding: 1em;
  margin-bottom: 1em;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.Wishlist-page header h3 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

.Wishlist-page header h3 span:last-of-type {
  color: #0f834d;
}

.Wishlist-page footer p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  margin-bottom: 1em;
  line-height: 1.25rem;
}

.Wishlist-page footer button {
  position: relative;
  color: #fff;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50px;
  background-color: rgba(0, 0, 0, 1);
}

.Wishlist-page footer button span {
  font-size: 1rem;
  display: block;
  margin: auto 0;
}

.Wishlist-page footer button img {
  position: absolute;
  top: 1em;
  right: 1em;
  height: 18px;
  /* margin: auto 0; */
}

.Wishlist-page footer div h4 {
  display: flex;
  justify-content: space-between;
}

.Wishlist-page footer div h4 span {
  display: block;
  width: 20%;
  height: 1px;
  margin: auto 0;
  background-color: rgba(0, 0, 0, 0.2);
}

.Wishlist-page footer div > img {
  margin-top: 1em;
}

.WishList-card {
  width: 300px;
  margin: 0 auto 1em;
  padding: 1em;
  background-color: #fff;
}

.WishList-card header {
  width: 100%;
}

.WishList-card header ul {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.WishList-card header li {
  font-weight: 600;
  margin: auto 0;
  line-height: 1.25rem;
}

.WishList-card header ul li:first-of-type {
  display: flex;
  width: 50px;
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.WishList-card header li > img {
  width: 40px;
  height: 40px;
  margin: auto;
}

.WishList-card header ul li:nth-child(2) {
  font-size: 0.875rem;
  width: 62%;
}
.WishList-card header ul li:last-of-type {
  display: flex;
  width: 32px;
  height: 32px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.WishList-card header ul li:last-of-type img {
  width: auto;
  height: 18px;
  margin: auto;
}

.WishList-card header p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  line-height: 1.25rem;
  margin: 0.8em 0;
}

.WishList-card footer > ul li {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.8em;
}

.WishList-card footer > ul li span {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  display: block;
  width: 100%;
}

.WishList-card footer > ul li select {
  width: 100%;
  margin: 0.8em 0;
  padding: 0.8em;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.WishList-card div h4 {
  font-size: 0.875rem;
  font-weight: 100;
  margin-bottom: 0.5em;
}

.WishList-card footer div div {
  display: flex;
  justify-content: space-between;
}

.WishList-card footer div div ul {
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.WishList-card footer div div ul li {
  padding: 0.8em 1em;
}

.WishList-card footer div div ul li:nth-child(2) {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.WishList-card footer div div h3 {
  margin: auto 0;
}

@media (min-width: 30em) {
  .Wishlist-page section {
    margin: 2em 0;
  }
  .WishList-card {
    position: relative;
    /* display: flex; */
    width: 86%;
    margin: 0 auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.03);
    background-color: #fff;
  }

  .WishList-card header {
    width: 100%;
    margin-bottom: 0;
  }

  .WishList-card header ul li:first-of-type {
    width: 70px;
    height: 70px;
    overflow: hidden;
  }

  .WishList-card header li > img {
    width: 70px;
    height: 70px;
  }

  .WishList-card header li {
    margin: 0;
  }

  .WishList-card header ul li:nth-child(2) {
    width: 80%;
    /* margin-top: 1em; */
  }

  .WishList-card header p {
    width: 70%;
    margin-top: -45px;
    margin-left: 82px;
  }

  .Wishlist-page header h3 {
    position: absolute;
    width: 180px;
    left: 12em;
    bottom: 0.7em;
  }

  .WishList-card > div {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .WishList-card footer > ul li {
    width: 48%;
  }

  .WishList-card footer > ul li select {
    margin: 0.5em 0 0;
    padding: 1em 1em;
  }

  .Wishlist-page footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    margin-top: 1em;
  }

  .Wishlist-page footer ul {
    width: 50%;
    display: flex;
    justify-content: space-between;
  }

  .WishList-card footer > ul li {
    width: 48%;
    display: inline-block;
  }

  .WishList-card footer div div {
    justify-content: baseline;
  }

  .WishList-card footer div div ul {
    width: 135px;
    margin-right: 0.8em;
  }

  .WishList-card footer div div ul li {
    width: 45px;
    height: 45px;
  }

  .WishList-card footer div div h3 {
    width: 70%;
  }

  .Wishlist-page footer > div {
    width: 48%;
    margin: 0;
  }
  .Wishlist-page footer button {
    width: 180px;
  }
}

@media (min-width: 50em) {
  .App .Wishlist-page {
    padding: 3em 7%;
    display: flex;
    justify-content: space-between;
  }

  .WishList-card {
    display: flex;
    justify-content: space-between;
  }

  .WishList-card header {
    width: 45%;
  }

  .WishList-card header ul li:nth-child(2) {
    width: 71%;
  }

  .WishList-card header h3 {
    left: 1em;
    bottom: 1.6em;
  }

  .Wishlist-page footer {
    width: 50%;
    margin: 0;
  }

  .Wishlist-page footer ul {
    width: 100%;
  }

  .Wishlist-page footer > div {
    width: 60%;
  }

  .WishList-card footer div div h3 {
    float: right;
    width: 70%;
  }

  .Wishlist-page footer button {
    margin: 1.4em 0;
  }
}
