.List-card {
  cursor: pointer;
  width: 100%;
  padding: 1em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
}

.List-card:hover {
  color: white;
  background-color: black;
  transition: ease-in 1s;
}

.List-card li {
  display: flex;
  justify-content: space-between;
}

.List-card li:first-of-type > img {
  width: 18px;
  height: 18px;
  margin: auto 0;
}

.List-card li:nth-child(2) {
  font-size: 0.875rem;
  display: inline-block;
  margin: auto 0;
  width: 70%;
  line-height: 1.25rem;
}

.List-card li:nth-child(2) span {
  display: block;
  width: 100%;
}

.List-card li:nth-child(2) span:last-of-type {
  font-weight: 500;
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.5);
}

.List-card:hover li:nth-child(2) span:last-of-type {
  color: red;
}

.List-card li:last-of-type {
  width: 50px;
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.List-card li:last-of-type img {
  width: 50px;
  height: 50px;
}

