.Breadcrumbs {
  color: white;
  display: flex;
  background-color: black;
  position: relative;
  top: 88px;
  left: 0;
  width: 100%;
  padding: 1em 7%;
  z-index: 1;
}

.Breadcrumbs li a {
  color: white;
  margin: 0 0.2em;
}

.Breadcrumbs li a:hover {
  text-decoration: underline;
}

@media (min-width: 30em) {
  .Breadcrumbs {
    top: 108px;
  }
}
