.Cart {
  width: 300px;
  margin: 0 auto;
  padding: 1em;
  background-color: #fff;
  margin-bottom: 1em;
}

.Cart header {
  width: 100%;
}

.Cart header ul {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.Cart header li {
  font-weight: 600;
  margin: auto 0;
  line-height: 1.25rem;
}

.Cart header ul li:first-of-type {
  display: flex;
  width: 50px;
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.Cart header li > img {
  width: 40px;
  height: 40px;
  margin: auto;
}

.Cart header ul li:nth-child(2) {
  font-size: 0.875rem;
  width: 62%;
}
.Cart header ul li:last-of-type {
  display: flex;
  width: 32px;
  height: 32px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.Cart header ul li:last-of-type img {
  width: auto;
  height: 18px;
  margin: auto;
}

.Cart header p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  line-height: 1.25rem;
  margin: 0.8em 0;
}

.Cart > div > ul > li {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.8em;
}

.Cart > div > ul > li span {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  display: block;
  width: 100%;
}

.Cart > div > ul > li select {
  width: 100%;
  margin: 0.8em 0;
  padding: 0.8em;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.Cart div h4 {
  font-size: 0.875rem;
  font-weight: 100;
  margin-bottom: 0.5em;
}

.Cart div div div {
  display: flex;
  justify-content: space-between;
}

.Cart div div div ul {
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.Cart div div div ul li {
  padding: 0.8em 1em;
}

.Cart div div div ul li:nth-child(2) {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.Cart div div div h3 {
  margin: auto 0;
}

@media (min-width: 30em) {
  .Cart {
    width: 84%;
    display: flex;
    flex-wrap: wrap;
    padding: 1.5em;
  }

  .Cart header {
    width: 100%;
    margin-bottom: 1em;
  }

  .Cart header ul li:first-of-type {
    width: 100px;
    height: 100px;
    overflow: hidden;
  }

  .Cart header li > img {
    width: 100px;
    height: 100px;
  }

  .Cart header li {
    margin: 0;
  }

  .Cart header ul li:nth-child(2) {
    width: 72%;
    margin-top: 1em;
  }

  .Cart header p {
    margin-top: -50px;
    margin-left: 120px;
  }

  .Cart > div {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .Cart > div > ul {
    width: 52%;
    display: flex;
    justify-content: space-between;
  }

  .Cart > div > ul > li {
    width: 48%;
  }

  .Cart > div > ul > li select {
    margin: 0.5em 0 0;
    padding: 1em 1em;
  }

  .Cart > div > div {
    width: 43%;
  }
}

@media (min-width: 30em) {
  .Cart header {
    margin-bottom: 1em;
  }
}
